@import "./mixin";

.home{
  .main{
    height:100vh;
    padding:5rem 0;
    box-sizing:border-box;
    .top{
      font-size:1.9rem;
      font-weight:normal;
      position:relative;
      &::after{
        content:'';
        position:absolute;
        top:1.2rem;
        left:19rem;
        width:42.5%;
        height:2px;
        background:#fff;
        animation:topline 1.5s ease-in;
      }
    }
    .scroll{
      padding-top:10rem;
      transform:scale(1, 0.7);
      span{
        position: absolute;
        top: 25rem;
        left: 50%;
        width: 24px;
        height: 24px;
        margin-left: -12px;
        border-left: 3px solid #fff;
        border-bottom: 3px solid #fff;
        -webkit-transform: rotate(-45deg);
        transform: rotate(-45deg);
        -webkit-animation: sdb 2s infinite;
        animation: sdb 2s infinite;
        opacity: 0;
        box-sizing: border-box;
        &:nth-of-type(1){
          -webkit-animation-delay: 0s;
          animation-delay: 0s;
        }
        &:nth-of-type(2){
          top:26rem;
          -webkit-animation-delay: .15s;
          animation-delay: .15s;
        }
        &:nth-of-type(3){
          top:27rem;
          -webkit-animation-delay: .3s;
          animation-delay: .3s;
        }
      }
    }
  }
  .about{
    display:flex;
    gap:5rem;
    margin-bottom:10vw;
    align-items:center;
    padding-left:8rem;
    h2{
      &.name{
        margin:2rem 0 1rem 0;
        text-align:center;
      }
    }
    .profileBox{
      span{
        width:10rem;
        height:10rem;
        border-radius:100%;
        display:inline-block;
        background: url(https://bomvomsss.github.io/first_portfolio/img/20201224_175629104_iOS.jpg) no-repeat center top / 110%;
        img{
          width:100%;
        }
      }
    }
    p{
      font-size:1.3rem;
      line-height:1.8rem;
    }
  }
  .career{
    padding-left:8rem;
  }
  .skill{
    padding-left:8rem;
    h2{
      margin:5rem 0 3.5rem;
    }
    p{
      font-size:1.4rem;
      font-weight:bold;
      margin:1em 0 0.8rem;
    }
    >div{
      display:flex;
      justify-content:left;
      flex-wrap:wrap;
      gap:0.4rem;
      margin-bottom:2rem;
    }
    span{
      padding:0.3rem 0.4rem;
      display:flex;
      align-items:flex-start;
      background:#fff;
      border-radius:0.2rem;
      color:#000;
      width:fit-content;
      svg{
        height:1.1rem;
        margin-right:0.2rem;
      }
    }
  }
  .license{
    padding-left:8rem;
    h2{
      margin:5rem 0 3.5rem;
    }
  }
  .licenseBox{
    display:flex;
    justify-content: left;
    margin-bottom:1.5rem;
    p{
      font-size:1.4rem;
      font-weight:bold;
      width:30%;
      i{
        font-weight:normal;
        font-size:1.1rem;
        font-style:normal;
      }
      span{
        display:block;
        font-size:1.2rem;
        font-weight:normal;
        margin-top:0.3rem;
      }
    }
    ul{
      margin-top:0.7rem;
      padding-left:1.3rem;
      li{
        font-size:1.1rem;
        text-decoration:none;
      }
    }
    ol{
      width:60%;
      li{
        width:100%;
        margin-bottom:3rem;
        font-size:1.3rem;
        font-weight:normal;
      }
      ul{
        &.flexBox{
          display:flex;
          flex-wrap:wrap;
          column-gap:2em;
          li{
            width:40%;
          }
        }
        li{
          font-size:1.1rem;
          margin-bottom:0.3rem;
          width:100%;
        }
      }
    }
  }
}

@keyframes topline{
  0%{width:0;}
  100%{width:42.5%;}
}
@-webkit-keyframes sdb {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes sdb {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes topline2{
  0%{width:0;}
  100%{width:19rem;}
}
@keyframes topline3 {
  0%{width:0;}
  100%{width:30px;}  
}
@include laptopToTablet{
  .home{
    .main{
      width:90vw;
      padding:3rem;
      .top{
        font-size:3em;
        &::after{
          top:2.3rem;
          left:10em;
          width:4.5em;
          height: 1px;
          animation:topline2 1.5s ease-in;
        }
      }
      h1{
        font-size:7em;
      }
      .scroll{
        span{
          top:15rem;
          &:nth-of-type(2){
            top:16rem
          }
          &:nth-of-type(3){
            top:17rem;
          }
        }
      }
    }
    .about{
      display: block;
      padding-left:0;
      .profileBox{
        text-align:center;
        margin-bottom:4rem;
        span{
          width: 13rem;
          height: 13rem;
        }
        h2.name{
          margin:1rem 0 0 0;
          font-size:3rem;
        }
      }
      p{
        font-size: 1.5em;
        line-height: 2.3rem;
      }
    }
    .career, .skill, .license{
      padding-left:0;
      .licenseBox{
        p{
        width:45%;
        }
        ol{
          ul{
            &.flexBox{
              li{
                width:43%;
              }
            }
          }
        }
      }
    }
  }
}
@include mobile{
  .home{
    .main{
      width:87vw;
      padding:3rem 0;
      height:100dvh;
      .top{
        font-size:1.8em;
        &::after{
          top:1rem;
          left:9.8em;
          width:30px;
          height:1px;
          animation:topline3 1s ease-in;
        }
      }
      h1{
        font-size:4em;
      }
      .scroll{
        span{
          top:38rem;
          &:nth-of-type(2){
            top:39rem
          }
          &:nth-of-type(3){
            top:40rem;
          }
        }
      }
    }
    .about{
      display: block;
      padding:0 0 3rem 0;
      .profileBox{
        text-align:center;
        margin-bottom:3rem;
        width:85dvw;
        span{
          width: 13rem;
          height: 13rem;
        }
        h2.name{
          margin:1rem 0 0 0;
          font-size:1.5rem;
        }
      }
      p{
        font-size:1em;
        line-height:1.8rem;
        letter-spacing:0.8;
        width:85dvw
      }
    }
    .career, .skill, .license{
      padding-left:0;
      h2{
        margin:8rem 0 3.5rem 
      }
      .licenseBox{
        display:block;
        width:70dvw;
        p{
        width:100%;
        }
        ol{
          width:100%;
          ul{
            width:100%;
            &.flexBox{
              li{
                width:43%;
              }
            }
            li{
              width:100%;
              font-size:1rem;
            }
          }
        }
      }
      p{
        margin-bottom:1.5rem;
        font-size:1.2rem;
      }
      .bestSkill, .normalSkill, .canSkill{
        width:90dvw;
        span{
          font-size:1.1rem;
        }
      }
    }
    .licenseBox{
      ol{
        li{
          font-size:1.2rem;
        }
      }
    }
  }
}